import { Component, ElementRef } from '@angular/core';
import { NavController } from 'ionic-angular';
import { SettingsPage } from '../settings/settings';
import { UtilProvider } from '../../providers/util/util';


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  data: any= {url : "https://igorfachini.github.io/test/webtest/templates/cover.html"};
  iframe: any;

  constructor(public navCtrl: NavController , private hostElement: ElementRef, public util: UtilProvider
  ) {
    console.log('this',this);
  }
  
  ngOnInit(){
    this.iframe = this.hostElement.nativeElement.querySelector('iframe');
    // this.iframe.progress(e => {
    //   console.log('progress',e);
    // });
    this.setUrl(this.data.url);
  }

  setUrl(url){
    this.util.showLoading();
    this.iframe.src = url;
  }

  t(e){
    console.log('testeeeeeeeee');
  }

  iframeOnLoad(e){
    console.log('iframeOnLoad',e);
    if(this.util.loading.instance){
      this.util.dismissLoading();
    }
  }

  openSettings(){
    this.navCtrl.push(SettingsPage);
  }


  openMenu(){
    this.setUrl("https://www.rotaexata.com.br/menu.html");
    console.log("menu");
  }

}



