import { AuthProvider } from './../../providers/auth/auth';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PatternLockPage } from '../pattern-lock/pattern-lock';
import { UtilProvider } from '../../providers/util/util';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  account: { email: string, senha: string } = {
    email: '47997478544',
    senha: 'qwe'
  };

  constructor(public navCtrl: NavController, public auth: AuthProvider, public navParams: NavParams, public util: UtilProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }

  // Attempt to login in through our User service
  doLogin() {
    this.util.showLoading();
    this.auth.login(this.account).subscribe((res: any) => {
      console.log("doLogin", res);
      // If the API returned a successful response, mark the user as logged in
      this.util.dismissLoading();
      if (res.status) {
        this.navCtrl.setRoot(PatternLockPage);
      } else {
        this.util.presentToast("(Erro) Login incorreto!");
      }
    }, err => {
      this.util.dismissLoading();
      this.util.presentToast("(Erro) Erro desconhecido!");

      console.error('doLogin ERROR', err);
    });
  }

}



